<template>
    <div>
        <div class="upload-list" v-for="(item, index) in files" :key="index">
            <template v-if="item.status">
                <slot name="item" :item="item">
                    <img :src="transformImgUrl(item.name)">
                </slot>
                <div class="upload-list-cover" v-if="!disabled">
                    <Icon type="ios-eye-outline" @click.native="handleView(item)"></Icon>
                    <Icon type="ios-trash-outline" @click.native="handleRemove(item)"></Icon>
                </div>
            </template>
            <template v-else>
                <Spin size="large" fix v-if="!item.status"></Spin>
            </template>
        </div>
        <Upload ref="upload" v-show="canUpload"
                :show-upload-list="false"
                :multiple="multiple"
                :before-upload="handleBeforeUpload"
                :type="type"
                :max-size="maxSize"
                :format="format"
                :accept="accept"
                action="#"
                style="display: inline-block;width:120px">
            <div style="width: 120px;height:120px;line-height: 120px;">
                <Icon type="ios-camera" size="20"></Icon>
            </div>
        </Upload>
        <Modal title="" v-model="visible">
            <slot name="preview" :item="preview" v-if="!!preview">
                <img :src="transformImgUrl(preview.name)" style="width: 100%">
            </slot>
        </Modal>
    </div>
</template>
<script>
import OSS from 'ali-oss';
import { FileSha1, platform, transformImgUrl } from '@/libs/common';
export default {
    name: 'OssUpload',
    props: {
        files: {
            type: Array
        },
        maxSize: {
            type: Number,
            default: Infinity
        },
        accept: {
            type: String
        },
        type: {
            type: String,
            default: 'drag'
        },
        format: {
            type: Array
        },
        multiple: {
            type: Boolean,
            default: false
        },
        onExceededSize: {
            type: Function,
            default: () => {}
        },
        onExceededQuantity: {
            type: Function,
            default: () => {}
        },
        onFormatError: {
            type: Function,
            default: () => {}
        },
        size: {
            type: Number,
            default: 0
        },
        defaultDir: {
            type: String
        },
        dirHandler: {
            type: Function,
            default: () => {}
        },
        disabled: {
            type: Boolean
        }
    },
    data () {
        return {
            transformImgUrl,
            preview: null,
            visible: false
        };
    },
    computed: {
        canUpload () {
            return !this.disabled && (this.files.length < this.size || this.files.length === 0);
        }
    },
    methods: {
        handleView (item) {
            this.preview = item;
            this.visible = true;
        },
        handleRemove (file) {
            this.files.splice(this.files.indexOf(file), 1);
        },
        handleBeforeUpload (file) {
            if (this.files.length >= this.size && this.size !== 0) {
                this.onExceededQuantity(file, this.files);
                return false;
            }

            // check format
            if (this.format.length) {
                const fileFormat = file.name.split('.').pop().toLocaleLowerCase();
                const checked = this.format.some(item => item.toLocaleLowerCase() === fileFormat);
                if (!checked) {
                    this.onFormatError(file, this.files);
                    return false;
                }
            }

            // check maxSize
            if (file.size > this.maxSize * 1024) {
                this.onExceededSize(file, this.files);
                return false;
            }
            this.uploadFile(file);
            return false;
        },
        async uploadFile (file) {
            const index = file.name.lastIndexOf('.');
            const suffix = index > -1 ? file.name.substring(index) : '';
            const sha1 = await FileSha1(file);
            if (!this.files.some(v => v === sha1)) {
                this.files.push({ status: false, sha1 });
                const sts = await this.fetch('/api/oss/sts');
                if (sts.success) {
                    const client = new OSS({
                        region: sts.data.region,
                        accessKeyId: sts.value.accessKeyId,
                        accessKeySecret: sts.value.accessKeySecret,
                        stsToken: sts.value.securityToken,
                        bucket: sts.data.imgBucket
                    });
                    let dir = this.dirHandler(file);
                    dir = typeof dir === 'string' ? dir : this.defaultDir || '';
                    dir = dir ? `${platform}/${dir}` : platform;
                    const result = await client.put(`${dir}/${sha1}${suffix}`, file);
                    const img = this.files.find(v => v.sha1 === sha1);
                    if (img) {
                        img.status = true;
                        img.url = result.url;
                        img.name = result.name;
                    }
                }
            }
        }
    }
};
</script>
<style lang="less">
    .upload-list {
        display: inline-block;
        width: 120px;
        height: 120px;
        text-align: center;
        line-height: 120px;
        border: 1px solid transparent;
        border-radius: 4px;
        overflow: hidden;
        background: #fff;
        position: relative;
        box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
        margin-right: 4px;
    }
    .upload-list img {
        width: 100%;
        height: 100%;
    }
    .upload-list-cover {
        display: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0, 0, 0, 0.6);
    }
    .upload-list:hover .upload-list-cover {
        display: block;
    }
    .upload-list-cover i {
        color: #fff;
        font-size: 20px;
        cursor: pointer;
        margin: 0 2px;
    }
</style>
